import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import Icon from '@mdi/react';
import { mdiBell } from '@mdi/js';
import { useHistory } from 'react-router-dom';
import useAuth from '../../services/useAuth';
import SearchComponent from '../SearchComponent';
import classNames from 'classnames';
import './styles.scss';


const CustomToggle = React.forwardRef(({ children, onClick }, ref) => (
  <li className="nav-item">
    <a className="btn"
      ref={ref}
      onClick={(e) => {
        e.preventDefault();
        onClick(e);
      }}
    >
      { children }
    </a>
  </li>
));

const NavbarComponent = ({ menuToggled }) => {
  const history = useHistory();
  const auth = useAuth();
  const [user, setUser] = useState(null);
  
  useEffect(() => {
    if (auth.isLoggedIn()) {
      auth.getUser().then(u => setUser(u));
    }
  }, [auth]);

  const handleNewPost = () => {
    history.push('/forum/create');
  }

  const handleLogout = () => {
    auth.logOut();
    window.location.href = '/forum';
  }

  return (
    <div className={classNames({ navbar: true, 'navbar-expanded': menuToggled})}>
      <SearchComponent />
      <button className="button">Ask Question</button>
      <button className="icon-button">
        <Icon path={mdiBell} size="23px" color="#7884F3" />
      </button>
    </div>
  );
};

NavbarComponent.props = {
  menuToggled: PropTypes.bool
};

NavbarComponent.defaultProps = {
  menuToggled: false
};

export default NavbarComponent;
